.headerArea
{
    margin-top: 20px;
    width: 100%;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
}

@media screen and (max-width:550px)
{
    .headerArea
    {
        flex-wrap: nowrap;
    }

    .headerImage
    {
        width: 0px;
        height: 0px;
    }
}

.headerTitleArea
{
    background-color: rgba(160, 160, 160, 0.171);
    padding-right: 3%;
    padding-left: 3%;
}

.title
{
    white-space: pre-line;
    font-weight: 100;
    font-size: 250%;
    margin-top: 15px;
    margin-bottom: 15px;
    letter-spacing: 1.5px;
    font-weight: 300;
    clear: both;
}

.verticalLine
{
    border-left: 1.6px outset black;
    height: 185px;
    margin-left: 10px;
}

.headerImage
{
    padding-top: 50px;
    height: 150px;
}

.headerCategoriesArea
{   
    display:inline-block;
    top: 40px;
    margin-left: auto;
    padding-right: 4%;
}


.listHeaderCategories
{

    padding-right: 4%;
    list-style-type:none;
}

.headerCategoryArea
{
    font-size: 100%;
    font-weight: 800;
    margin-bottom: 5px;
}

.headerCategoryFont
{
    font-weight: 100;
    cursor: pointer;
}

.headerCategoryFont:hover
{
    color: rgb(151, 151, 151);
}

.horizontalLine
{
    width: 100%;
    height: 1.6px;
    background-color: black;
    margin-top: 10px;
    margin-bottom: 20px;
}